@media screen and (max-width : 600px) {
  .logo{
    height: 75%;
    width:100%;
    margin-left: 20%;
    padding:1%;
  }
  .imgtext{
    font-size: 17px;
  }

  .slide2{
    padding:15%;
  }

  .cropname{
    text-align: center;
    font-size: 28px;
    font-family: "Poppins", Arial, Tahoma, sans-serif;
    }
  .crop{
      display: block;
      margin: auto;
      height: 18%;
      width:18%;
      
    }
    .lan3text{
      font-size: 20px;
      font-family: "Poppins", Arial, Tahoma, sans-serif;
      margin-left: 10%;
    }
    .crop2{
      margin:auto;
      height: 70%;
    }
    .landing3{
      background-size: 100% auto;
      /* background-position: left top; */
    }
}

